import { createElement } from "@wordpress/element";
import classNames from "classnames";
import PropTypes from "prop-types";
import { forwardRef } from "@wordpress/element";

/**
 * @param {boolean} [disabled=false] Whether the input is disabled.
 * @param {string} [rows] Textarea rows (height).
 * @param {string} [className=""] CSS class.
 * @returns {JSX.Element} Textarea component.
 */
const Textarea = forwardRef(({
  disabled,
  rows,
  className,
  ...props
}, ref) => createElement("textarea", {
  ref: ref,
  disabled: disabled,
  rows: rows,
  className: classNames("nfd-textarea", disabled && "nfd-textarea--disabled", className),
  ...props
}));
const propTypes = {
  className: PropTypes.string,
  disabled: PropTypes.bool,
  cols: PropTypes.number,
  rows: PropTypes.number
};
Textarea.propTypes = propTypes;
Textarea.defaultProps = {
  className: "",
  disabled: false,
  cols: 20,
  rows: 2
};

// eslint-disable-next-line require-jsdoc
export const StoryComponent = props => createElement(Textarea, {
  ...props
});
StoryComponent.propTypes = propTypes;
StoryComponent.defaultProps = Textarea.defaultProps;
StoryComponent.displayName = "Textarea";
export default Textarea;